import dueDateFn from "../reducers/reducer-duedate-data";

const dueDate = dueDateFn();

export default function(state = dueDate, action) {
  let newState;
  console.log("duedate oldState", state);
  switch (action.type) {
    case "ADD_DUE":
      newState = state.concat({
        due: action.payload.due
      });
      console.log("ADD_DUE newState", newState);
      return newState;

    case "DELETE_DUE":
      newState = state.filter(item => item.due != action.payload.due);
      console.log("DELETE_DUE newState", newState);
      return newState;
  }
  return state;
}
